export default {
	data() {
		return {
			//输入框样式的激活状态
			accAcitve: false,
			//输入框样式的激活状态
			pwdAcitve: false,
			login: [{
					text1: "登录"
				},
				{
					text2: "注册账号"
				},{
					text3:'拓客新零售'
				}
			]
		};
	},
	/**
	 * Vue的组件存放
	 */
	components: {},
	/**
	 * uni-app生命周期
	 * options是上个页面的传参
	 * 指的是页面加载完毕执行的函数
	 */
	onLoad(options) {},
	/**
	 * uni-app
	 * 指的是页面获取焦点的时候执行的函数
	 */
	onShow() {},
	/**
	 * uni-app
	 * 指的是页面失去焦点的时候执行的函数
	 */
	onHide() {},
	/**
	 * Vue计算属性
	 * 存放的函数，里面的函数名是自己定义的。函数必须有个返回值
	 */
	computed: {},
	/**
	 * Vue监听器
	 * 存放的函数，里面的函数名通常是data的数据
	 * 监听data的数据，一旦发生改变就执行里面对应的函数
	 */
	watch: {},
	/**
	 * Vue的自定义方法
	 */
	methods: {
		// 		onClick:function(){
		//
		// 	console.log("123456")
		// uni.navigateTo({
		//     url: '../register/register',
		// })
		// 		}
		submitverify() {
			uni.navigateTo({
				url: '../home/home'
			});
		},
		onClick() {
			uni.navigateTo({
				url: '../register/register'
			});
		},
